import api from '@/api'
export function pageTo(path){
	vk.navigateTo(path);
}

//
export async function addCart(productId,productNum) {
	//
	if(!vk.checkToken()) {
		vk.navigateTo('/pages/me/me')
		return
	} else {
		await api.addCart({
			productId,
			productNum
		})
		vk.toast("","success")
	}
}